* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  background-image: url('../img/world-67861_1280.png');
  background-size: cover;
  background-repeat: no-repeat;
}

ul {
  list-style: none;
}
#search-box {
  font: inherit;
  padding: 8px;
  width: 500px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.country-list {
  width: 500px;
  display: flex;
  flex-direction: column;
  gap: 20px;

  background-color: rgba(67, 66, 67, 0.6);

  margin: auto;
}
.country-wrapper {
  display: flex;

  align-items: center;
  gap: 20px;
}

.title {
  font-size: 24px;
  color: black;
  font-weight: 700;
  text-shadow: 0px 1px 1px #777b7a;
}
.countryInfoText {
  color: #fff;
  font-weight: 500;
  display: block;
}
.singlCountryInfo {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
